<template>
	<div class="row">
		<app-name v-for="(name, index) in names" @click.native="deleteName(index)" key="name.id">
		<div class="d-flex justify-content-between align-items-center">
			<p>Name:</p>
			<span>{{ name.name }}</span>	
		</div>
		<div class="d-flex justify-content-between align-items-center">
			<p>Hours:</p>
			<span>{{ name.hours }}</span>
		</div>
		<div class="d-flex justify-content-between align-items-center final-tip">
			<p>Tips:</p>
			<span>{{ name.final }} $</span>
		</div>
		</app-name>
	</div>	
</template>


<script>
	import Name from './Name.vue';
	export default {
		props: ['names'],
		components: {
			appName: Name
		},
		methods: {
			deleteName(index){
				this.$emit('nameDeleted', index);
			}
		}
	}

</script>


<style scoped>
	p {
		margin: 6px 0;
		border: 2px solid green;
		padding: 6px;
	}

	.final-tip p {
		border: 2px solid red;
	}
</style>


